@tailwind base;
@tailwind components;
@tailwind utilities;
@import url('https://fonts.googleapis.com/css2?family=Nanum+Pen+Script&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+TC:wght@400;600;700&display=swap');

body,
.p {
  @apply text-sm leading-7 md:text-base xl:text-lg xl:leading-8;
}

.text-title {
  @apply text-base md:text-lg xl:text-2xl;
}

.text-heading {
  @apply text-2xl md:text-[32px] xl:text-[46px] xl:leading-[60px];
}

.text-small-title {
  @apply text-xs leading-6;
}
